.history-card {
  max-width: 490px;
  display: grid;
  grid-template-columns: minmax(auto, 250px) 150px;
  gap: 30px;
  padding: 30px;
  background: var(--color-ebony-clay);
  border-radius: 10px;

  &__border {
    grid-column: 2 span;
    height: 1px;
    background-color: #5E6074;
  }

  &__name {
    font-size: 18px;
    line-height: 22px;
    color: var(--color-light);
  }

  &__bank {
    font-size: 18px;
    line-height: 22px;
    color: var(--color-light);
  }

  &__info {
    display: flex;
    flex-direction: column;
    gap: 9px;
  }

  &__text {
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    color: var(--color-gray);
  }

  &__num {
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
    color: #FFFFFF;
  }
}

@media (max-width: 576px) {
  .history-card {
    padding: 10px;
    gap: 10px;

    &__name {
      font-size: 14px;
    }
  
    &__bank {
      font-size: 14px;
    }

    &__text {
      font-size: 12px;
    }

    &__num {
      font-size: 12px;
    }
      
    
  }
  
}